import React, { Component } from 'react';
import './loading.less';
import { connect } from 'react-redux';
import { getLoadingShow } from 'store/module/loading';

const FPS = 25;

class LoadingComponent extends Component {
    constructor(props) {
        super(props)
        this.state = {
            dirIndex: window.dirIndex || 0
        }
   }
   componentDidMount() {
      this.anima = window.setInterval(()=>{
          let dirIndex = this.state.dirIndex;
          dirIndex++;
          this.setState({dirIndex})
          if(dirIndex === 55) {
              if(window.changePage) {
                window.dirIndex = dirIndex;
                window.changePage()
              }
          }
          if(dirIndex>76) {
            window.dirIndex = 0;
            this.props.toggleLoading(false)
          } 
      }, 1000/FPS);
   }

   componentWillUnmount() {
        window.clearInterval(this.anima);
   }

   render() {
       return (
           <img src={`image/loading/loading${this.state.dirIndex}.png`} className="loading-root" alt=""/>
       )
   }
}

export default connect(state => ({
    loading: getLoadingShow(state)
  }),
  dispatch => ({
      toggleLoading: (show) => dispatch({ type: "LOADING_SHOW", doc: { show } })
  })
)(LoadingComponent);