import React, { Component } from 'react';
import "./home.less"
import TitlePng from 'assets/img/title.png';
import Loading from 'views/Loading/Loading';
import { connect } from 'react-redux';
import { getLoadingShow } from 'store/module/loading';

class HomeComponent extends Component {
    constructor(props) {
        super(props)
        this.state = {}
        window.changePage = this.changePage
   }

   handleClick = (menu) => {
        this.props.setMenu(menu)
        this.props.toggleLoading(true)
   }

   changePage = () => {
        this.props.history.push("/main")
   }

   render() {
       return (
           <React.Fragment>
            <div className="home-root">
                <video src={`video/home/home.mp4`} width="100%" height="100%" style={{objectFit: "fill"}} muted autoPlay loop/>
                <img className="title" src={TitlePng} alt="" />
                <div className="zodiac" onClick={()=>{this.handleClick("zodiac")}}>
                </div>
                <div className="sky">
                </div>
            </div>
            { this.props.loading && <Loading/>  }
          </React.Fragment>
       )
   }
}

export default connect(state => ({
    loading: getLoadingShow(state)
  }),
  dispatch => ({
      toggleLoading: (show) => dispatch({ type: "LOADING_SHOW", doc: { show } }),
      setLoadingChange: (changeFunc) => dispatch({ type: "LOADING_CHANGE", doc: { changeFunc } }),
      setMenu: (menu) => dispatch({ type: "SET_MENU", doc: { menu } })
  })
)(HomeComponent);